const initialState = {
    adults: 0,
    children: 0

  }
 export const occupancy = (state = initialState, action, selectValue) => {
      switch (action.type){
            case 'NUMBER_ADULTS':
            console.warn("action done")
                return {
                    ...state,
                    adults: selectValue,
                }
            case 'NUMBER_CHILDREN':
            console.warn("action done")
                return {
                    ...state,
                    children: selectValue,
                }                
            
          default:
              return state
      }
  }

  export default occupancy